webpackJsonp([142629428675168],{

/***/ 414:
/***/ (function(module, exports) {

	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"html":"<p>On large software projects, communication is key. There are so many ways we can try to facilitate regular communication between engineering teams, but I believe one of the best is regular rotation of engineers between the teams. Working on a new team fosters knowledge sharing, shared ownership and responsibility, better technical decisions, and builds communication and technical skills for each of the engineers.</p>\n<p>I define knowledge sharing between software teams as understanding what other teams do: their goals, process, products, and code. Understanding each of these things enables effective communication and planning on your own team. When someone new is rotated onto the team, they have the opportunity to see these processes first-hand, perform them, and improve them with an outside perspective. Performing something yourself is a great way to actually understand it. When that new person rotates off the team, their understanding will also become out of date, but now that can be resolved with a conversation with someone now on that team. Now they know who to talk to, and their shared base of understanding enables them to have that conversation more effectively than starting from scratch.</p>\n<p>A shared base of understanding also contributes to shared ownership and responsibility for the entire project. When things go wrong, we don’t always get the opportunity to learn what went wrong, how it was resolved, and how to prevent it in the future. Missing these leave us with a lack of confidence in our ability to troubleshoot and resolve future issues, and result in a culture of avoiding issues that we don’t understand. This culture spreads very quickly. Regular rotation between teams provides a lot more opportunity to observe these issues and learn from them. It builds a network of people to talk to, and that shared base of understanding that you can use to figure out an issue even without immediately recognizing it.</p>\n<p>Regular rotation between teams builds a network of people to talk to organically. Knowing who to talk to about a given topic is invaluable, but difficult to build from scratch. We have the opportunity to find this network every day that we work closely with several people. Increasing that number by rotating between teams helps that network grow so you can be more confident in finding the answer to your problem. Working with new people regularly helps you learn to communicate with different people who have difference communication styles. Likewise, working with new codebases helps you learn different patterns and how to fit your own style into a new team.</p>\n<p>These are all the reasons I think regular team rotation is very important. It’s difficult to implement for several reasons, like reluctance from individuals, aligning individuals with managers, and balancing staffing needs with department goals. So I don’t like making hard rules about rotation, but instead keeping it as a goal and metric to be tracked and discussed with each individual and team regularly.</p>\n<p>I’ve tried hard to keep pair programming out of this discussion so far, but bear with me now. I truly believe team rotation is essential to a healthy team, while pair programming is a little more optional. But pairing will multiply these results and make them much more visible and permanent. Pairing often involves rotation within a team that achieves these same results, so rotation between teams is a natural extension.</p>","fields":{"slug":"/regular-team-rotation.html","post":{"title":"Regular Team Rotation","link":null,"publishedAtTimestamp":"2017-07-05T17:00Z","publishedAt":"July 5, 2017 5:00pm UTC"}}}},{"node":{"html":"<p>Usually when I need documentation for a RubyGem I'm working with, I go to <a href=\"http://www.rubydoc.info/gems\">RubyDoc.info</a>.\nToday, however, it was not responding.\nAfter catching up on Twitter it occurred to me that I could read the documentation locally since I already had the gem installed.</p>\n<p>Running <code>gem rdoc prawn</code><sup id=\"fnref-1\"><a href=\"#fn-1\" class=\"footnote-ref\">1</a></sup> generates documentation for the prawn gem.\nThen <code>gem server</code> will start a server on <code>localhost:8808</code> hosting the generated RDoc.</p>\n<p>This worked fine, but it occurred to me that starting and stopping the gem server whenever I want to see the documentation is annoying.\nOS X has the concept of <a href=\"https://developer.apple.com/library/mac/documentation/MacOSX/Conceptual/BPSystemStartup/Chapters/CreatingLaunchdJobs.html\">launch agents</a> which can be kept alive by the operating system whenever you are logged in.\nI thought I would make a launch agent to run a gem server so that I could open it whenever I wanted.</p>\n<p>Unfortunately, running <code>gem server</code> in a default shell uses the version of Ruby that ships with OS X.\nI have gems installed in different versions of Ruby using <a href=\"https://github.com/postmodern/chruby\">chruby</a>, and I might want to see any of them depending on what project I'm working on.\nSo I decided to run a server for each version of Ruby I have installed (<em>except</em> the system Ruby).\nEach server runs on a different port chosen based on the Ruby version it supports.\nRuby 2.0.0 is available at <code>localhost:8200</code>, 2.1.4 is available at <code>localhost:8214</code>, and 2.2.1 is at <code>localhost:8221</code>.</p>\n<p>I wrote a script and an accompanying launch agent plist to run it and <a href=\"https://gist.github.com/adamstegman/76d9cb40a8a3d92ea0e6\">posted them in a gist</a>.</p>\n<div class=\"footnotes\">\n<hr>\n<ol>\n<li id=\"fn-1\">\n<p>There was one catch to generating the documentation. I keep my <code>~/.gemrc</code> with <code>gem: --no-ri --no-rdoc</code> so that bundle installs go quickly, but in this case it was causing the <code>gem rdoc</code> command to fail. I had to remove that option to successfully generate the documentation.</p>\n<a href=\"#fnref-1\" class=\"footnote-backref\">↩</a>\n</li>\n</ol>\n</div>","fields":{"slug":"/launchd-gem-servers-on-os-x.html","post":{"title":"Launchd Gem Servers on OS X","link":null,"publishedAtTimestamp":"2015-04-21T04:29Z","publishedAt":"April 21, 2015 4:29am UTC"}}}},{"node":{"html":"<p>While working on photo uploads in my <a href=\"https://github.com/adamstegman/photo_albums\">latest project</a>, I needed a network activity indicator to reflect background network usage.\nThat way, users could navigate away from the upload form and see that uploads were still going in the background.\nSince I'm using <a href=\"http://emberjs.com/\">Ember.js</a> I created a component to display the indicator.\nI created an array in my <code>ApplicationController</code> to track each upload.\nFor each element in the array, I rendered a network activity indicator.</p>\n<p>The concept was pretty straightforward and in a simple case worked fine.\nBut when I removed an indicator after an upload was done, I got a strange error:</p>\n<pre><code>TypeError: Cannot read property 'destroy' of undefined\n    at Ember.CollectionView.Ember.ContainerView.extend.arrayWillChange (http://localhost:8888/assets/ember.js?body=true:23233:16)\n    at null._contentWillChange (http://localhost:8888/assets/ember.js?body=true:23140:10)\n    at sendEvent (http://localhost:8888/assets/ember.js?body=true:2302:14)\n    at notifyBeforeObservers (http://localhost:8888/assets/ember.js?body=true:2671:5)\n    at Object.propertyWillChange (http://localhost:8888/assets/ember.js?body=true:2497:3)\n    at set (http://localhost:8888/assets/ember.js?body=true:2763:15)\n    at Object.Ember.trySet (http://localhost:8888/assets/ember.js?body=true:2832:10)\n    at Object.Binding._sync (http://localhost:8888/assets/ember.js?body=true:6765:15)\n    at Object.DeferredActionQueues.flush (http://localhost:8888/assets/ember.js?body=true:5565:24)\n    at Object.Backburner.end (http://localhost:8888/assets/ember.js?body=true:5656:27)\n</code></pre>\n<p>The stacktrace indicated the problem is when I <code>set</code> the new array of uploads.\nFrom the description of <a href=\"http://emberjs.com/api/classes/Ember.CollectionView.html\"><code>Ember.CollectionView</code></a> it looked like the problem is with updating the rendered template from the array:</p>\n<blockquote>\n<p><code>Ember.CollectionView</code> is an <code>Ember.View</code> descendent responsible for managing a collection (an array or array-like object) by maintaining a child view object and associated DOM representation for each item in the array and ensuring that child views and their associated rendered HTML are updated when items in the array are added, removed, or replaced.</p>\n</blockquote>\n<p>Stack Overflow had <a href=\"http://stackoverflow.com/a/16661864\">an answer</a> that explained that the problem was with updating the property bindings:</p>\n<blockquote>\n<p><code>push</code> doesn't tell the binding to update, but <code>pushObject</code> does</p>\n</blockquote>\n<p>Since I was using a native JavaScript array, I was using native methods to add and remove uploads.\nI needed to use an <code>Ember.NativeArray</code> instead so that I could use it in my template.\nThis is possible using <code>Ember.A</code> to wrap an array-like object:</p>\n<pre><code>this.set('uploadActivityIndicators', Ember.A([id]));\nthis.get('uploadActivityIndicators').removeObject(id);\n</code></pre>\n<p>From what I've read, this is typical of Ember—use Ember's replacements for native JavaScript concepts to avoid any surprises like this.\nThis is one reason why some people prefer Angular or React.\nI don't think this is enough of a reason to write off a framework, it's just a guideline of using Ember.\nIt makes it more complicated to learn but doesn't make it automatically bad.\nStill, I need to learn more about Angular and React so I can better compare the options.</p>","fields":{"slug":"/rendering-array-properties-in-ember-js.html","post":{"title":"Rendering array properties in Ember.js","link":null,"publishedAtTimestamp":"2014-08-23T23:39Z","publishedAt":"August 23, 2014 11:39pm UTC"}}}},{"node":{"html":"<p>I created an <a href=\"https://developer.apple.com/library/mac/documentation/cocoa/reference/ApplicationKit/Classes/NSTokenField_Class/Reference/Reference.html\">NSTokenField</a> and everything worked great with strings as tokens.\nWhen I created a representative class to use instead, I ended up with an error message and a blank token field.</p>\n<pre><code>&#x3C;NSTokenFieldCell: 0x6080003c97e0>: Failed to query display string for a represented object &#x3C;HMCPhotoTag: 0x608000422460>. Ignoring...\n</code></pre>\n<p>Turns out, I just set the <a href=\"https://developer.apple.com/library/mac/documentation/Cocoa/Reference/ApplicationKit/Classes/NSControl_Class/Reference/Reference.html#//apple_ref/occ/instm/NSControl/objectValue\"><code>objectValue</code></a> before I set the delegate.\nSetting the delegate first meant the token field could transform my objects correctly.</p>","fields":{"slug":"/nstokenfield-failed-to-query-display-string-for-a-represented-object.html","post":{"title":"NSTokenField failed to query display string for a represented object","link":null,"publishedAtTimestamp":"2013-12-02T01:19Z","publishedAt":"December 2, 2013 1:19am UTC"}}}},{"node":{"html":"<p>Originally posted as a <a href=\"https://gist.github.com/adamstegman/5077010\">gist</a> in response to <a href=\"https://twitter.com/adamstegman/status/307944394445688832\">this twitter conversation</a>.</p>\n<p>I'm working on a Rails application that uses the <a href=\"http://archive.msdn.microsoft.com/ewsjavaapi\">Exchange Webservices API</a>. Unfortunately the EWS API has a weird package, but I thought that wouldn't be hard to overcome. So I wrote this script:</p>\n<pre><code>require 'java'\nrequire 'vendor/lib/EWSJavaAPI_1.2.jar'\n\ndef microsoft\n  Java::Microsoft\nend\n\nversion = microsoft.exchange.webservices.data.ExchangeVersion::Exchange2010_SP1\nservice = microsoft.exchange.webservices.data.ExchangeService.new(version)\n</code></pre>\n<p>Getting the ExchangeVersion enum worked fine, but trying to access the ExchangeService class resulted in this error:</p>\n<blockquote>\n<pre><code>NameError: missing class or uppercase package name (`microsoft.exchange.webservices.data.ExchangeService')\nget_proxy_or_package_under_package at org/jruby/javasupport/JavaUtilities.java:54\nmethod_missing at file:/Users/adam/.rvm/rubies/jruby-1.7.3/lib/jruby.jar!/jruby/java/java_package_module_template.rb:14\n(root) at lib/sandbox.rb:11\n</code></pre>\n</blockquote>\n<p>I thought the problem was with the package name, but the error is actually that the EWS API is missing its dependencies. It needed commons-httpclient and commons-logging-api on the classpath. I discovered this via clojure's error message for similar code:</p>\n<pre><code>(import '(microsoft.exchange.webservices.data ExchangeService))\n</code></pre>\n<blockquote>\n<pre><code>ClassNotFoundException org.apache.commons.httpclient.HttpConnectionManager  java.net.URLClassLoader$1.run (URLClassLoader.java:366)\n</code></pre>\n</blockquote>\n<p>So the real issue here is JRuby's error message. Interestingly enough, JRuby did give me a better error once commons-httpclient was on the classpath:</p>\n<blockquote>\n<pre><code>MultiThreadedHttpConnectionManager.java:70:in `&#x3C;clinit>': java.lang.NoClassDefFoundError: org/apache/commons/logging/LogFactory\n</code></pre>\n</blockquote>\n<p>That told me commons-logging-api was also missing. JRuby should have told me that in the first place.</p>","fields":{"slug":"/jruby-nameerror-missing-class-or-uppercase-package-name.html","post":{"title":"JRuby NameError: missing class or uppercase package name","link":null,"publishedAtTimestamp":"2013-04-13T19:43Z","publishedAt":"April 13, 2013 7:43pm UTC"}}}},{"node":{"html":"<blockquote>\n<p>Imagine if we didn’t judge the author of the code by how few “WTFs” it generated, but instead by how fertile it was\nfor discussion? Or, imagine if every programmer was being judged by what they learned from the discussion. How would\nthat change things? better still, imagine if the “manager's” job was to facilitate the learning and get the\ndiscussion back on track when it strayed into fixing-the-code territory instead of to judge people by how much they\n\"contribute.\" That would be a breath of fresh air to most teams.</p>\n</blockquote>\n<p>An excellent point. Code reviews are valuable for getting eyes on the code, but I agree with Raganwald here that the\nmore valuable outcome is that everyone shares in the learning. I've thought of that as learning <em>about</em> the work that\nothers are doing, but Raganwald makes a good point that you learn from the <em>discussion</em> about the work that others are\ndoing.</p>","fields":{"slug":"/code-reviews.html","post":{"title":"Code Reviews","link":"http://raganwald.posterous.com/a-conjecture-about-code-reviews","publishedAtTimestamp":"2012-10-05T00:20Z","publishedAt":"October 5, 2012 12:20am UTC"}}}},{"node":{"html":"<blockquote>\n<p>pair programming works best with a large uncertain search space of problems and solutions. the closer to a solved\nproblem, the less it helps</p>\n</blockquote>\n<p>— <a href=\"https://twitter.com/KentBeck\">Kent Beck</a> (<a href=\"https://twitter.com/KentBeck\">@KentBeck</a>)</p>","fields":{"slug":"/kent-beck-on-pair-programming.html","post":{"title":"Kent Beck on Pair Programming","link":"https://twitter.com/KentBeck/status/253532726714580992","publishedAtTimestamp":"2012-10-04T01:29Z","publishedAt":"October 4, 2012 1:29am UTC"}}}},{"node":{"html":"<p>I'm writing a class in <a href=\"http://adamstegman.com/projects/git-push.html\">Git Push</a> to hold and fetch git blobs from\n<a href=\"http://developer.github.com/v3/git/blobs/\">Github</a>.</p>\n<pre><code>@interface GHBlob : NSObject &#x3C;NSCopying, NSMutableCopying>\n@property (nonatomic, copy, readonly) NSData *content;\n...\n@end\n\n@implementation GHBlob\n@synthesize content = _content;\n...\n@end\n</code></pre>\n<p>To support creating and updating blobs, I wrote a mutable subclass initialized\nin #mutableCopy from <a href=\"http://developer.apple.com/library/ios/#documentation/Cocoa/Reference/Foundation/Protocols/NSMutableCopying_Protocol/Reference/Reference.html#//apple_ref/occ/intf/NSMutableCopying\">NSMutableCopying</a>. This entailed\nredefining properties as <code>readwrite</code>:</p>\n<pre><code>@interface GHMutableBlob : GHBlob\n@property (nonatomic, copy, readwrite) NSData *content;\n...\n@end\n\n@implementation GHMutableBlob\n@synthesize content = _content;\n...\n@end\n</code></pre>\n<h3>Problems</h3>\n<h4>Resynthesizing</h4>\n<p>As shown above, I also re-synthesized the methods in the subclass. However,\nthis resulted in the subclass's initializer breaking.</p>\n<pre><code>GHBlob *newBlob = [blob mutableCopy];\nSTAssertEquals(newBlob.content, blob.content, nil);\n  //=> '&#x3C;00000000>' should be equal to '&#x3C;10cbb806>'\n</code></pre>\n<p>On review, I noticed something I'd missed before in\n<a href=\"http://developer.apple.com/library/mac/#documentation/Cocoa/Conceptual/ObjectiveC/Chapters/ocProperties.html\">Apple's documentation on redeclaring properties</a>. The redeclared\nproperty isn't synthesized in their example, it's declared <code>@dynamic</code> and the\nsetter is defined by hand. I scoffed at the inconvenience (isn't this what\nsynthesizing is supposed to avoid?), but did it anyway.</p>\n<h4>Unreachable instance variables</h4>\n<p>Next, the compiler complained that the <code>_content</code> instance variable was\nunreachable. Oh,\n<a href=\"http://stackoverflow.com/questions/5015130/do-properties-have-to-be-declared-as-instance-variables-in-objective-c#answer-5015657\">so <em>this</em> is why we define ivars in the interface</a> - it makes\nthem available to subclasses.</p>\n<h3>Summary</h3>\n<p>So to implement <code>NSMutableCopying</code>, I had to explicitly declare my instance\nvariables in the interface and explicitly define setter methods in my mutable\nsubclass. I thought the property/synthesize syntax was\n<a href=\"http://mobile.dzone.com/news/open-letter-apple-please-kill\">saving me the trouble</a>, but apparently it's not all rainbows and\nunicorns.</p>\n<pre><code>@interface GHBlob : NSObject &#x3C;NSCopying, NSMutableCopying> {\n  NSData *_content;\n}\n@property (nonatomic, copy, readonly) NSData *content;\n...\n@end\n\n@implementation GHBlob\n@synthesize content = _content;\n@end\n</code></pre>\n<p>And in the mutable subclass:</p>\n<pre><code>@interface GHMutableBlob : GHBlob\n@property (nonatomic, copy, readwrite) NSData *content;\n...\n@end\n\n@implementation GHMutableBlob\n@dynamic content;\n- (void)setContent:(NSData *)content {\n  _content = [content copy];\n}\n</code></pre>","fields":{"slug":"/implementing-a-mutable-subclass-in-objective-c.html","post":{"title":"Implementing a Mutable Subclass in Objective-C","link":null,"publishedAtTimestamp":"2012-01-06T06:31Z","publishedAt":"January 6, 2012 6:31am UTC"}}}},{"node":{"html":"<p>While I was working on unit tests for <a href=\"http://adamstegman.com/projects/git-push.html\">Git Push</a>, I wanted to mock my\ndelegate so I could create an expectation. So I used <a href=\"http://ocmock.org/\">OCMock</a>:</p>\n<pre><code>id delegate = [OCMockObject mockForProtocol:@protocol(MyDelegateProtocol)];\n[[delegate expect] someMethod];\nmyClassIvar.connectionDelegate = delegate;\n[myClassIvar someOtherMethod];\nSTAssertNoThrow([delegate verify], @\"should have called someMethod on delegate\");\n</code></pre>\n<p>But found the assertion failing. I stepped into <code>#someOtherMethod</code> and discovered\nthe delegate was nil. So I added</p>\n<pre><code>STAssertNotNil(myClassIvar.connectionDelegate, @\"should have set delegate\");\n</code></pre>\n<p>And watched that fail as well. Since Git Push is using <a href=\"http://developer.apple.com/technologies/ios5/\">ARC</a>, I <a href=\"http://clang.llvm.org/docs/AutomaticReferenceCounting.html#objects\">read up\non retain semantics</a> to see if I was missing something about local\nreferences. But local references are strong, so the delegate should not have\nbeen deallocated yet.</p>\n<p>I <a href=\"http://stackoverflow.com/questions/8675054/why-is-my-objects-weak-delegate-property-nil-in-my-unit-tests\">posted about this issue on Stack Overflow</a> <sup id=\"fnref-1\"><a href=\"#fn-1\" class=\"footnote-ref\">1</a></sup>, where Evan\nsuggested that OCMock may be the issue. Sure enough, creating an explicit class\nconforming to my delegate protocol fixed the issue.</p>\n<pre><code>@interface MockDelegate : NSObject &#x3C;MyDelegateProtocol>\n@property (nonatomic, strong) NSNumber *called;\n@end\n@implementation MockDelegate\n@synthesize called = _called;\n- (void)someMethod {\n  _called = [NSNumber numberWithBool:YES];\n}\n@end\n\n@implementation MyClassTests\n- (void)testSomeOtherMethod {\n  MockDelegate *delegate = [[MockDelegate alloc] init];\n  myClassIvar.connectionDelegate = delegate;\n  [myClassIvar someOtherMethod];\n  STAssertTrue([delegate.called boolValue], @\"should have called someMethod on delegate\");\n}\n@end\n</code></pre>\n<p>I still don't understand why it was being deallocated, but it was definitely\nrelated to OCMock. I've isolated the issue in <a href=\"https://github.com/adamstegman/ARCMock\">a Github repo</a> to make\nit obvious and repeatable.</p>\n<p>UPDATE: Erik Doernenberg of OCMock looked into the issue and answered the Stack\nOverflow question as well, and determined it's actually an issue with NSProxy\nobjects and ARC in the iOS runtime. It works fine on OS X.</p>\n<div class=\"footnotes\">\n<hr>\n<ol>\n<li id=\"fn-1\">\n<p>Note that my code is slightly different, as I was using a static helper function to create my mock. I confirmed that was not the issue.</p>\n<a href=\"#fnref-1\" class=\"footnote-backref\">↩</a>\n</li>\n</ol>\n</div>","fields":{"slug":"/nil-weak-reference-to-an-ocmock-mock-object.html","post":{"title":"Nil Weak Reference to an OCMock Mock Object","link":null,"publishedAtTimestamp":"2012-01-02T21:55Z","publishedAt":"January 2, 2012 9:55pm UTC"}}}},{"node":{"html":"<p>Yesterday Nathan Fillion <a href=\"http://twitter.com/NathanFillion/statuses/106853202909073409\">tweeted</a> about finding a permutation of his name on a website. At first I just chuckled, but it soon occurred to me that this would be a fun problem to solve with scripting.</p>\n<p>So I whipped up <a href=\"https://gist.github.com/1174851\">fillion.rb</a> to peruse a webpage for permutations of a given word. It was fun to figure out a quick algorithm to find permutations, and then fight scope creep (I ended up writing a crawler for an entire domain).</p>\n<p>The beauty of development is being able to look for a challenge anywhere.</p>","fields":{"slug":"/fillion-rb.html","post":{"title":"fillion.rb","link":"https://gist.github.com/1174851","publishedAtTimestamp":"2011-08-27T01:48Z","publishedAt":"August 27, 2011 1:48am UTC"}}}},{"node":{"html":"<p>While working on <a href=\"http://adamstegman.com/projects/web-queue.html\">Web Queue</a> today I went to document the URLs to\nhelp me figure out the interface. As I was about to start writing out the URLs,\nI realized I could make it a lot easier on myself by giving the document some\nstructure. So I decided to roll it into the README, which also needed to be\nwritten.</p>\n<p>As I wrote up the README, everything became a bit clearer. Up to that point, I\nhad been fussing over details, learning how to use OAuth and the Netflix API\nwhile poorly maintaining a few different classes. Writing the README allowed me\nto step back and conceive the system as a whole, which made the necessary code\nmuch more clear to me. That's README-driven development.</p>\n<p>My projects often go like this. I'm almost always learning a new technology or\nlibrary, so I can't always start off with a good architecture. But starting\nwith the interface can make things a lot clearer.</p>\n<p>Writing out the URLs for a project is pretty similar to writing the README. It\nhas the same goal, figuring out the interface of a project, but focused on web\napplications instead of any other project. And as I'm learning from personal\nexperience, that can be exactly what's needed for a good project.</p>","fields":{"slug":"/url-driven-development-is-readme-driven-development.html","post":{"title":"URL-Driven Development is README-Driven Development","link":null,"publishedAtTimestamp":"2011-08-06T19:42Z","publishedAt":"August 6, 2011 7:42pm UTC"}}}},{"node":{"html":"<blockquote>\n<p>I am very excited by the Internet and how easy it is today to get a project started.\nCase in point, <a href=\"http://adamstegman.com/\">my new site</a>.</p>\n</blockquote>\n<p>I'm also hosting <a href=\"http://blog.adamstegman.com/\">my blog</a> there now.</p>","fields":{"slug":"/new-site.html","post":{"title":"New Site","link":"http://adamstegman.com/","publishedAtTimestamp":"2011-08-04T01:56Z","publishedAt":"August 4, 2011 1:56am UTC"}}}},{"node":{"html":"<p>I’ve unfortunately been experiencing the magic of Grails lately.</p>\n<p>TIL:</p>\n<pre><code>new JSONObject([key: []]) == new JSONObject([key: []])\n</code></pre>\n<p>and</p>\n<pre><code>new JSONArray([]) == new JSONArray([])\n</code></pre>\n<p>but</p>\n<pre><code>new JSONObject([key: new JSONArray([])]) != new JSONObject([key: new JSONArray([])])\n</code></pre>\n<p>This is so much fun.</p>\n<p>Update: This is due to a non-implementation of <code>JSONArray.hashCode()</code>. JIRA logged here: <a href=\"http://jira.grails.org/browse/GRAILS-7770\">http://jira.grails.org/browse/GRAILS-7770</a></p>","fields":{"slug":"/til-grails-edition.html","post":{"title":"TIL, Grails edition","link":null,"publishedAtTimestamp":"2011-07-21T17:38Z","publishedAt":"July 21, 2011 5:38pm UTC"}}}},{"node":{"html":"<p>Reading code documentation to understand a system is like learning how to drive by inspecting the washer fluid dispenser, then a spark plug, then the brake pads. Write a README.</p>","fields":{"slug":"/write-a-readme.html","post":{"title":"Write a README","link":null,"publishedAtTimestamp":"2011-07-13T06:09Z","publishedAt":"July 13, 2011 6:09am UTC"}}}},{"node":{"html":"<blockquote>\n<p>And, in good conscience, don’t you want to be an idiot when you’re on the other side of the screen? I do! I want to be\nan idiot! Please let me be an idiot. I want things to “just work”.</p>\n</blockquote>\n<p>Completely agree. It's easy to lose sight of this.</p>","fields":{"slug":"/90-percent-of-your-users-are-idiots.html","post":{"title":"90% of your users are \"idiots\"","link":"http://blog.jitbit.com/2011/06/90-of-your-users-are-idiots.html","publishedAtTimestamp":"2011-06-29T17:56Z","publishedAt":"June 29, 2011 5:56pm UTC"}}}},{"node":{"html":"<p>I've written several tests that have the unfortunate side effect of writing to stdout or stderr, polluting my pretty stream of dots.</p>\n<p>Thanks to a <a href=\"http://benevolentcode.com/2011/03/temporarily-redirect-stdout-in-ruby/\">post on Benevolent Code</a>, I decided to silence them (<a href=\"https://gist.github.com/adamstegman/926853\">gist</a>).</p>\n<pre><code>setup :silence_output\n\n# Redirects stderr and stdout to /dev/null.\ndef silence_output\n  @orig_stderr = $stderr\n  @orig_stdout = $stdout\n\n  # redirect stderr and stdout to /dev/null\n  $stderr = File.new('/dev/null', 'w')\n  $stdout = File.new('/dev/null', 'w')\nend\n\nteardown :enable_output\n\n# Replace stdout and stderr so anything else is output correctly.\ndef enable_output\n  $stderr = @orig_stderr\n  $stdout = @orig_stdout\n  @orig_stderr = nil\n  @orig_stdout = nil\nend\n</code></pre>\n<p>Update:</p>\n<p>The same thing in RSpec (<a href=\"https://gist.github.com/926858\">gist</a>):</p>\n<pre><code>RSpec.configure do |config|\n  config.before(:all, &#x26;:silence_output)\n  config.after(:all, &#x26;:enable_output)\nend\n\n# Redirects stderr and stdout to /dev/null.\ndef silence_output\n  @orig_stderr = $stderr\n  @orig_stdout = $stdout\n\n  # redirect stderr and stdout to /dev/null\n  $stderr = File.new('/dev/null', 'w')\n  $stdout = File.new('/dev/null', 'w')\nend\n\n# Replace stdout and stderr so anything else is output correctly.\ndef enable_output\n  $stderr = @orig_stderr\n  $stdout = @orig_stdout\n  @orig_stderr = nil\n  @orig_stdout = nil\nend\n</code></pre>","fields":{"slug":"/silence-test-unit-tests.html","post":{"title":"Silence Test::Unit tests","link":null,"publishedAtTimestamp":"2011-04-19T04:20Z","publishedAt":"April 19, 2011 4:20am UTC"}}}},{"node":{"html":"<p>I'm working on reading emails sent from ActionMailer, and was noticing the weird form of what I thought was URL-encoding in an HTML section of a multipart email, e.g.</p>\n<pre><code>&#x3C;html lang=3D\"en\">\n</code></pre>\n<p>I also noticed the line length and endings:</p>\n<blockquote>\n<p>blah blah … blah bl=<br>\nah blah blah</p>\n</blockquote>\n<p>I shrugged these off as quirks of ActionMailer or SMTP and moved on to removing/decoding them.</p>\n<p>Two hours later, when googling for info about them because they didn't seem consistent, I stumbled across <a href=\"http://en.wikipedia.org/wiki/Quoted-printable\">Quoted-printable encoding</a>. I recognized it from the headers in the email, and wondered if someone had written a gem or something to decode it. Then I find <a href=\"http://rubydoc.info/stdlib/core/1.8.7/String#unpack-instance_method\">this</a> - String#unpack was able to do it for me all along.</p>\n<p>It's great that Ruby (and sometimes Rails) can do things like this out of the box. I just wish I was smart enough to look.</p>","fields":{"slug":"/quoted-printable-in-ruby.html","post":{"title":"Quoted-printable in Ruby","link":null,"publishedAtTimestamp":"2011-02-10T03:31Z","publishedAt":"February 10, 2011 3:31am UTC"}}}},{"node":{"html":"<p>I'll admit it. I hate test-driven development while I'm actually doing it. It's boring and tedious. I get lost in details while trying to get some test to work. Maybe I just need to do more <a href=\"http://thecleancoder.blogspot.com/2010/10/craftsman-62-dark-path.html\">katas</a>, but it gets old fast.</p>\n<p>Katas make it look so nice and easy. You just think of the right test, write it, and make it pass. But in practice I have a much bigger picture in mind that I want to implement, and I just can't limit myself to building it one atom at a time, or I'll lose it. Sometimes, I just want to hack.</p>\n<p>So I can sympathize when I see people writing a bunch of code to get some massive feature ready, and then filling in tests at the end. But it's wrong. It seems harmless (\"I'm still testing, right?\") because the difference can be subtle. Writing tests after the fact means you're writing your tests to fit your code, instead of the other way around. You're trying to achieve that code coverage metric instead of thinking hard about each feature, branch, and interface.</p>\n<p>Just like writing code without TDD, it can often work just fine. But it's not scalable and it results in poorly-tested and/or poorly-written code.</p>\n<p>I'm not just preaching, I live this dilemma every day. TDD sucks, but man, does it ever improve the quality of my code.</p>\n<p>Sometimes I just hack. But most of the time, I drop in TODO's where I need to remember a big picture and get to testing first.</p>","fields":{"slug":"/test-driven-drudgery.html","post":{"title":"Test-Driven Drudgery","link":null,"publishedAtTimestamp":"2011-01-05T21:56Z","publishedAt":"January 5, 2011 9:56pm UTC"}}}},{"node":{"html":"<p>I made an about.me page. At the very least, it will be a central repository for where to find me. Hopefully I can get a better background eventually.</p>","fields":{"slug":"/about-me.html","post":{"title":"About.me","link":"http://about.me/adamstegman","publishedAtTimestamp":"2010-12-03T07:30Z","publishedAt":"December 3, 2010 7:30am UTC"}}}},{"node":{"html":"<p>Rails' ActiveRecord has a nifty <code>#destroy</code> method that almost everyone already knows about. The documentation for <code>#destroy</code><sup id=\"fnref-1\"><a href=\"#fn-1\" class=\"footnote-ref\">1</a></sup> helpfully states</p>\n<blockquote>\n<p>Deletes the record in the database and freezes this instance to reflect that no changes should be made (since they\ncan’t be persisted).</p>\n</blockquote>\n<p>What is less clear is how it works when you call it on a relationship. To be sure, it destroyed the database record. What it also unfortunately does it hangs on to the reference in the parent object.</p>\n<p>Let's throw down with some sample code.</p>\n<pre><code>class Thing &#x3C; ActiveRecord::Base\n  has_one :child\nend\n\nclass Child &#x3C; ActiveRecord::Base\n  belongs_to :thing\nend\n\nzengarden> thing = Thing.new\n=> #&#x3C;Thing id: nil, name: nil, created_at: nil, updated_at: nil>\nzengarden> thing.save\n=> true\nzengarden> thing.create_child :name => \"Child\"\n=> #&#x3C;Child id: 4, name: \"Child\", thing_id: 1,...>\nzengarden> thing.child\n=> #&#x3C;Child id: 4, name: \"Child\", thing_id: 1...>\nzengarden> thing.child.destroy\n=> #&#x3C;Child id: 4, name: \"Child\", thing_id: 1...>\nzengarden> thing.child\n=> #&#x3C;Child id: 4, name: \"Child\", thing_id: 1...>\n</code></pre>\n<p>Arg. I just destroyed it! I don't want it hanging around in my cache. If I really wanted to save it, I should have saved it before or during the destroy call. I can of course invalidate my cache by calling</p>\n<pre><code>thing.child(true)\n</code></pre>\n<p>which correctly returns nil. But if I don't know to do that immediately after the destroy, I get errors like</p>\n<blockquote>\n<p>TypeError: can't modify frozen hash</p>\n</blockquote>\n<p>because code used later naïvely calls</p>\n<pre><code>if thing.child\n  thing.child.attributes = new_attributes\nend\n</code></pre>\n<p>expecting that if a child exists, it should be updated.</p>\n<p>For the sake of argument, let's say that maybe the behavior isn't so bad, maybe this is what people expect or could use. In that case, what a terrible error message. I'm not familiar enough with the Associations internals to suggest where a better one could be injected, but something along the lines of</p>\n<blockquote>\n<p>Can't modify a destroyed record</p>\n</blockquote>\n<p>would be miles better.</p>\n<div class=\"footnotes\">\n<hr>\n<ol>\n<li id=\"fn-1\">\n<p>Rails 3 - <a href=\"http://railsapi.com/doc/rails-v3.0.0/classes/ActiveRecord/Persistence.html#M001019\">http://railsapi.com/doc/rails-v3.0.0/classes/ActiveRecord/Persistence.html#M001019</a>\nRails 2.3.8 - <a href=\"http://railsapi.com/doc/rails-v2.3.8/classes/ActiveRecord/Base.html#M001111\">http://railsapi.com/doc/rails-v2.3.8/classes/ActiveRecord/Base.html#M001111</a></p>\n<a href=\"#fnref-1\" class=\"footnote-backref\">↩</a>\n</li>\n</ol>\n</div>","fields":{"slug":"/activerecord-hangs-on-to-destroyed-relations.html","post":{"title":"ActiveRecord Hangs On to Destroyed Relations","link":null,"publishedAtTimestamp":"2010-10-25T18:31Z","publishedAt":"October 25, 2010 6:31pm UTC"}}}},{"node":{"html":"<p>Version 1.1.0 of Read Link Later now works with New Twitter. Rejoice!</p>\n<p>Sorry for the delay. New Twitter came to me at a bad time, where I didn't have the time to devote to updating Read Link Later. Fortunately, we're in business now!</p>\n<p>The only issue is that the New Twitter uses their own API through JavaScript, so I don't have a way of knowing when a tweet is loaded. To work around this, I look for new tweets to add the Read Later links to every 100 ms. If I could hack in a custom event, that would be something.</p>","fields":{"slug":"/read-link-later-works-with-new-twitter.html","post":{"title":"Read Link Later Works With New Twitter","link":"https://chrome.google.com/extensions/detail/mffbgadohiiggcibdgmogfdlmackfbhm","publishedAtTimestamp":"2010-10-20T12:27Z","publishedAt":"October 20, 2010 12:27pm UTC"}}}},{"node":{"html":"<p><a href=\"http://github.com/thoughtbot/factory_girl\"><code>factory_girl</code></a> is great for DRYing up test code and making tests isolated and maintainable. What it's not so great at is any <a href=\"http://api.rubyonrails.org/classes/ActiveRecord/Associations/ClassMethods.html\">association</a> more complicated than <code>has_one</code>/<code>belongs_to</code>. I found <a href=\"http://stackoverflow.com/questions/2937326/populating-an-association-with-children-in-factory-girl\">a nice trick on Stack Overflow</a> for <code>has_many</code> associations.</p>\n<blockquote>\n<p>The Factory.after_ hooks appear to be the only way to do this successfully.</p>\n</blockquote>\n<p>It's a shame, because it makes it really painful to use factories. What used to be a one-line call to create whatever you need becomes writing your own factories that call <code>factory_girl</code> factories.</p>","fields":{"slug":"/complex-associations-with-factory-girl.html","post":{"title":"Complex Associations with factory_girl","link":null,"publishedAtTimestamp":"2010-08-18T19:00Z","publishedAt":"August 18, 2010 7:00pm UTC"}}}},{"node":{"html":"<p>I'm excited to publish my first Google Chrome extension, Read Link Later. From the detail page:</p>\n<blockquote>\n<p>Read Link Later adds an Instapaper \"Read Later\" link to each tweet in a Twitter.com feed. The link retrieves each external URL in the tweet and adds it to your Instapaper queue for reading later.</p>\n</blockquote>\n<p>Unfortunately I can't add these links to clients, that would be even more helpful.</p>","fields":{"slug":"/read-link-later.html","post":{"title":"Read Link Later","link":"https://chrome.google.com/extensions/detail/mffbgadohiiggcibdgmogfdlmackfbhm","publishedAtTimestamp":"2010-08-16T04:35Z","publishedAt":"August 16, 2010 4:35am UTC"}}}},{"node":{"html":"<p><a href=\"http://iwasamonkey.tumblr.com/post/915222642/happiness\">iwasamonkey</a>:</p>\n<blockquote>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 370px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 345.94594594594594%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Happiness\"\n        title=\"\"\n        src=\"/static/happiness-336082001386c47b498ecc3d2928bd6b-f2c47.jpg\"\n        srcset=\"/static/happiness-336082001386c47b498ecc3d2928bd6b-cdfcb.jpg 240w,\n/static/happiness-336082001386c47b498ecc3d2928bd6b-f2c47.jpg 370w\"\n        sizes=\"(max-width: 370px) 100vw, 370px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Happiness</p>\n</blockquote>","fields":{"slug":"/happiness.html","post":{"title":"Happiness","link":null,"publishedAtTimestamp":"2010-08-07T03:17Z","publishedAt":"August 7, 2010 3:17am UTC"}}}},{"node":{"html":"<p><a href=\"http://onethingwell.org/post/898477013/gobolinux\">onethingwell</a>:</p>\n<blockquote>\n<blockquote>\n<p>GoboLinux is a modular Linux distribution: it organizes the programs in your system in a new, logical way. Instead of\nhaving parts of a program thrown at <code>/usr/bin</code>, other parts at <code>/etc</code> and yet more parts thrown\nat</p>\n<pre><code>/usr/share/something/or/another\n</code></pre>\n<p><em>each program gets its own directory tree</em>, keeping them all neatly separated and allowing you to see everything\nthat’s installed in the system and which files belong to which programs in a simple and obvious way.</p>\n</blockquote>\n<p>Off-topic, I know, but this strikes me as an eminently sensible approach.</p>\n</blockquote>\n<p>Sounds like what OS X does with applications. Very sensible.</p>","fields":{"slug":"/gobolinux.html","post":{"title":"GoboLinux","link":"http://www.gobolinux.org/","publishedAtTimestamp":"2010-08-03T15:19Z","publishedAt":"August 3, 2010 3:19pm UTC"}}}},{"node":{"html":"<p>The more I think about this, the more this statement bothers me. <a href=\"http://twitter.com/tavon\">@tavon</a> said that grid layouts like <a href=\"http://960.gs/\">960 grid</a> are as bad as table layouts. His reasoning was that the classes are purely presentational; it's presentation affecting markup the same way tables used to.</p>\n<p>But that's just misuse of the layouts. You shouldn't pull something massive and generic like a grid layout and not modify it to suit your website. Wouldn't you modify your reset stylesheet to match the styles your website uses? Use your existing class names instead of the presentational grid class names, and remove the classes you don't need. These third party tools are not holy relics - change them to what you need!</p>\n<p>That doesn't mean they can't or don't affect markup inappropriately. I haven't used a grid layout, so I'm not familiar with what's required. But the generated styles aren't inherently bad, they just need to be adapted.</p>","fields":{"slug":"/grid-layout-as-bad-as-table-layout.html","post":{"title":"Grid Layout As Bad As Table Layout?","link":null,"publishedAtTimestamp":"2010-07-17T05:06Z","publishedAt":"July 17, 2010 5:06am UTC"}}}},{"node":{"html":"<p>UPDATE: After deleting and re-cloning RubyAMP, I was able to edit the shortcut. All good!</p>\n<p>I've had to uninstall <a href=\"http://code.leadmediapartners.com/\">RubyAMP</a>, a TextMate bundle that I use several times a day, because it overrode Ctrl+B and refused to let me change it.</p>\n<p>I realize most people don't use these shortcuts, so I'll explain it. Ctrl+B moves the cursor back one character, the equivalent of the left arrow key without having to move your hands. My workflow uses Ctrl+B so constantly that I couldn't live with RubyAMP anymore, even though I enjoy its functionality.</p>\n<p>The reason this is so negative is the fact that Ctrl+B is not a shortcut that I added; it's part of OS X. If you are creating keyboard shortcuts for your program or extension, you can't possibly avoid stepping on every other program's toes. But the least you can do is avoid stepping on the operating system's toes. Otherwise it's just negligent, user-hostile design.</p>","fields":{"slug":"/dont-override-system-shortcuts.html","post":{"title":"Don't Override System Shortcuts","link":null,"publishedAtTimestamp":"2010-06-29T14:31Z","publishedAt":"June 29, 2010 2:31pm UTC"}}}},{"node":{"html":"<blockquote>\n<p>This viral ad for <em>Civilization V</em> about people trying to kick their <em>Civ</em> habit will probably just make you want to\npick the game up again.</p>\n</blockquote>\n<p>Wow, it's almost like that's what they intended.</p>\n<p><a href=\"http://www.escapistmagazine.com/news/view/101565-Civ-Anonymous-Where-You-Go-to-Stop-Taking-Another-Turn\">Source</a></p>","fields":{"slug":"/thanks-captain-obvious.html","post":{"title":"Thanks, Captain Obvious","link":null,"publishedAtTimestamp":"2010-06-22T20:43Z","publishedAt":"June 22, 2010 8:43pm UTC"}}}},{"node":{"html":"<p>Mark asserts that while using onclick is frowned upon, it's faster and cleaner:</p>\n<blockquote>\n<p>Sure it makes your XHTML a bit longer, but that’s one less selector jQuery has to parse and one less DOMReady function\nyour browser has to kick off.</p>\n</blockquote>\n<p>That's <em>true</em>, but it comes at the cost of maintainability. Putting the jQuery example he provided into an external file makes it easier to maintain by having all the JavaScript in predictable and findable places.</p>\n<p>Additionally, that performance hit can be mitigated by using progressive enhancement to give the entire page functionality immediately, then asynchronously loading the JavaScript to enhance it. Asynchronously loading your JS may or may not even be necessary, depending on your JS payload.</p>\n<p>Using inline code like this isn't necessary for most people; they just make your code harder to read.</p>","fields":{"slug":"/onclick-is-your-friend.html","post":{"title":"onclick is Your Friend","link":"http://thenerdary.net/articles/entry/onclick-is-your-friend","publishedAtTimestamp":"2010-06-21T23:00Z","publishedAt":"June 21, 2010 11:00pm UTC"}}}},{"node":{"html":"<blockquote>\n<p>You know what would annoy the shit out of me if I were using a screen reader to read a bibliography on the internet?\nListening to a goofy-sounding computer voice try to emphasize every single word of <em>Journal of the Study of Obscure\nand Mostly-in-Latin Canine Diseases Affecting Generally the Respiratory System but Also Sometimes the Lymph Nodes</em> or\nsomething. I don’t know that this is still a problem, but the idea is ridiculous in and of itself. Italic does not\nalways mean emphasis, nor does bold always mean MAKE THIS LOUD.</p>\n</blockquote>\n<p>Hey, you know, that's true. That's what &#x3C;b> and &#x3C;i> should be used for. Here's what they <em>shouldn't</em> be used for, from the same article:</p>\n<blockquote>\n<p>The bold and italic tags are short. The emphasis tag isn’t bad, but the strong tag is way too long to be a good value\nper keystroke.</p>\n</blockquote>\n<p>Yeah, semantic HTML uses tags that are longer than one character. &#x3C;strong> is five whole more letters than &#x3C;b>! Sounds like someone who throws around a lot of \"u\"instead of \"you\", and \"y\" instead of \"why\" in textual conversations. Not someone I'd want to take design advice from.</p>\n<blockquote>\n<p>I add</p>\n<pre><code>&#x3C;b>\n</code></pre>\n<p>and</p>\n<pre><code>&#x3C;i>\n</code></pre>\n<p>to things I don’t necessarily want bolded or italicized. I do it because they’re some of the shortest elements\navailable and they provide a hook to style child elements in larger repeated widgets with very precise, complex\nlayouts. If I have to wrap every word of text in a tag just to accomplish some goofy layout, I’m using the smallest\nthing available.</p>\n</blockquote>\n<p>That sounds like a pretty poor layout.</p>\n<p>Bottom line is, &#x3C;b> and &#x3C;i> have their place, but it's not showing emphasis, nor is it saving keystrokes or because this layout is kinda complicated and I want some easy hooks.</p>","fields":{"slug":"/defending-terrible-things.html","post":{"title":"Defending Terrible Things","link":"http://garann.wordpress.com/2010/06/19/defending-terrible-things/","publishedAtTimestamp":"2010-06-20T05:14Z","publishedAt":"June 20, 2010 5:14am UTC"}}}},{"node":{"html":"<p>And no one wants to put in that much work.</p>","fields":{"slug":"/comments-are-hard-work.html","post":{"title":"Comments are hard work.","link":"http://powazek.com/posts/1063","publishedAtTimestamp":"2010-06-17T15:13Z","publishedAt":"June 17, 2010 3:13pm UTC"}}}},{"node":{"html":"<p>The top-voted comments here are a great example of why people are agreeing with <a href=\"http://daringfireball.net/2010/06/whats_fair\">John Gruber's assessment of comments</a>.</p>\n<blockquote>\n<p>What makes DF an efficient and effective soapbox is exactly that it is <em>not</em> noisy.\nMy goal is for not a single wasted word to appear anywhere on any page of the site.</p>\n</blockquote>\n<p>Those comments distract from the article with fiery, irrelevant rants that show the authors didn't even RTFA. The whole point of the article is lost if you start reading them.</p>","fields":{"slug":"/this-is-why-we-cant-have-nice-things.html","post":{"title":"This is why we can't have nice things.","link":"http://blogs.hbr.org/bregman/2010/06/why-i-returned-my-ipad.html","publishedAtTimestamp":"2010-06-17T15:08Z","publishedAt":"June 17, 2010 3:08pm UTC"}}}}]},"site":{"siteMetadata":{"title":"My Kind of Stupid"}}},"pathContext":{}}

/***/ })

});


